import  axios  from 'axios'
import React, { useEffect, useState } from 'react'

export default function Home() {


  let [trndingMovies , setTrendingMovies] = useState([])
  let [trndingTv , setTrendingTv] = useState([])
  let [trndingPeople , setTrendingPeople] = useState([])

  let img = 'https://image.tmdb.org/t/p/w500'

  async function getMovies(media  , callback){
    let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${media}/week?api_key=f1aca93e54807386df3f6972a5c33b50`)
    callback(data.results.slice(0,10))
    } 


    async function getPerson(callback){
      let {data} = await axios.get(`https://api.themoviedb.org/3/person/popular?api_key=f1aca93e54807386df3f6972a5c33b50&language=en-US&page=1`)
      
      callback(data.results.slice(0,10))
      } 

  useEffect(()=> {
    getMovies('movies' , setTrendingMovies)
    getMovies('tv' , setTrendingTv)
  getPerson(setTrendingPeople)
  }
    , [])
  return (
    <>
    <div className='row'>

      <div className="col-md-4 flex">
        <div className='border my-3 w-25 '></div>

      <h2 className='h3'>Trending Movies 
    <br />
    To Watch 

    <br />
    Right Now </h2>
    <p className='text-muted'>Trending Movies To Watch</p>
    <div className='border my-3'></div>
      </div>
 {trndingMovies.map((movie,index)=> <div key={index}  className="col-md-2">  
 
    <div className="movie">
      <img className='w-100' src={img + movie.poster_path} alt={movie.title} />
    <h3 className='h6 my-2'>{movie.title}</h3>
       </div>
       
 
    </div>)}
    
     
    </div>
    <div className='row my-5'>

      <div className="col-md-4 flex">
        <div className='border my-3 w-25 '></div>

      <h2 className='h3'>Trending Tv 
    <br />
    To Watch 

    <br />
    Right Now </h2>
    <p className='text-muted'>Trending Tv To Watch</p>
    <div className='border my-3'></div>
      </div>
 {trndingTv.map((movie,index)=> <div key={index}  className="col-md-2">  
 
    <div className="movie">
      <img className='w-100' src={img + movie.poster_path} alt={movie.title} />
    <h3 className='h6 my-2'>{movie.name}</h3>
       </div>
       
 
    </div>)}
    
     
    </div>
    <div className='row'>

      <div className="col-md-4 flex">
        <div className='border my-3 w-25 '></div>

      <h2 className='h3'>Trending People 
    <br />
    To Watch 

    <br />
    Right Now </h2>
    <p className='text-muted'>Trending People To Watch</p>
    <div className='border my-3'></div>
      </div>
 {trndingPeople.map((movie,index)=> <div key={index}  className="col-md-2">  
 
    <div className="movie">
      <img className='w-100' src={img + movie.profile_path} alt={movie.title} />
    <h3 className='h6 my-2'>{movie.name}</h3>
       </div>
       
 
    </div>)}
    
     
    </div>
    </>
  
  )
}
