import  axios  from 'axios'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';

export default function Login(props) {


  let x = props.Data




  let navigate = useNavigate();
  function handleClick() {
    navigate("/home");
  }

  
  let [isLoading , setIsLoading] = useState(false) 


 let [error,setError] = useState('')
 let [ValidError,setValidErro] = useState([])


let [user , setuser] = useState({email:'',password:''})


function getUser({target}){

  let myUser = {...user}

  myUser[target.name] = target.value

  setuser(myUser)
}


  async function sendDataApi(e){
    setValidErro([])
    setIsLoading(true)
    e.preventDefault()
  
    let validateResponse = validateForm()
  
    if(validateResponse.error){
      setValidErro(validateResponse.error.details)
      setIsLoading(false)
    
    }else{
      let {data} = await axios.post(`https://route-egypt-api.herokuapp.com/signin` , user)
  
      if(data.message === 'success'){
        localStorage.setItem('userToken' , data.token)
        x()
        handleClick()
      }else{
        setError(data.message)
        setIsLoading(false)
      }
    }
  
   
  }


  function validateForm(){
    let  Joi = require('joi');
    let scheme = Joi.object({
      email:Joi.string().email({tlds: { allow: ['com', 'net'] } }).required(),
      password:Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$'))
    })
  
    return scheme.validate(user)
  }


  return (
    <div>
      <div className="w-75 m-auto">

{error &&<div className="alert alert-danger">{error}</div>}

{<div>{ValidError.map((error , index) => <div key={index} className='alert alert-danger'>{error.message}</div> )}</div>}

<form onSubmit={sendDataApi} >

<label htmlFor="email">email</label>
<input onChange={getUser} className='form-control' type="email" name='email' />
<label htmlFor="password">password</label>
<input onChange={getUser}  className='form-control'type="password" name='password' />

<button type='submit' className='btn btn-info mt-3'>
  
  {isLoading?<i className="fas fa-spinner fa-spin"></i>: 'login'}
  </button>


</form>


</div>
    </div>
  )
}

