import { Route, Routes, useNavigate } from 'react-router-dom';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import Home from './components/Home/Home';
import Movies from './components/Movies/Movies';
import Tv from './components/Tv/Tv';
import Register from './components/Register/Register';
import Login from './components/Login/Login';
import People from './components/People/People';
import Netwrok from './components/Network/Netwrok';
import Notfound from './components/Notfound/Notfound';
import jwtDecode from 'jwt-decode';
import React, { useEffect, useState } from 'react'






function App() {

  let [loginUser , setLoginUser] = useState(null);

  function userData(){

 let  Data = localStorage.getItem('userToken')
 let  user =  jwtDecode(Data)
   setLoginUser(user)

  }

  useEffect(()=>{
      if(localStorage.getItem('userToken')){
        userData();
      }
 }, [])

 let navigate = useNavigate();

  function logMeOut() {
    navigate("/login");
  }
function logOut(){
  localStorage.removeItem('userToken')
  setLoginUser(null)
  logMeOut()
}
 

  return (
    <div>
    
     <Navbar user={loginUser} logOut={logOut}/>
     <div className='container'>
     <Routes>

       {loginUser!= null ? <>
        <Route path='/home' element={<Home/>} />
        <Route path='/movies' element={<Movies/>} />
        <Route path='/tv' element={<Tv/>} />
        <Route path='/people' element={<People/>} />
        <Route path='/network' element={<Netwrok/>} />
        <Route path='/' excat element={<Home/>} />
        <Route path='*' element={<Notfound/>} />
       </>: <Route path='*' element={<Login Data={userData}/>} />}

       
       <Route path='/register' element={<Register/>} />
       <Route path='/login' element={<Login Data={userData}/>} />
   
     </Routes>
     </div>  
     
    
    </div>
  );
}

export default App;
