import  axios  from 'axios'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';


export default function Register() {

  let navigate = useNavigate();
  function handleClick() {
    navigate("/login");
  }

  


 let [error,setError] = useState('')
 let [ValidError,setValidErro] = useState([])


let [user , setuser] = useState({first_name:'',last_name:'',age:'',email:'',password:''})


function getUser({target}){

  let myUser = {...user}

  myUser[target.name] = target.value

  setuser(myUser)
}



async function sendDataApi(e){
  e.preventDefault()

  let validateResponse = validateForm()

  if(validateResponse.error){
    setValidErro(validateResponse.error.details)
  
  }else{
    let {data} = await axios.post(`https://route-egypt-api.herokuapp.com/signup` , user)

    if(data.message === 'success'){
      handleClick()
    }else{
      setError(data.message)
    }
  }

 
}

function validateForm(){
  let  Joi = require('joi');
  let scheme = Joi.object({first_name:Joi.string().min(3).max(30).required(),
    last_name:Joi.string().min(3).max(30).required(),
    age:Joi.number().min(16).max(80).required(),
    email:Joi.string().email({tlds: { allow: ['com', 'net'] } }).required(),
    password:Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$'))
  })

  return scheme.validate(user)
}


  return (
    <div>
      
    <div className="w-75 m-auto">

      {error &&<div className="alert alert-danger">{error}</div>}
     
      {<div>{ValidError.map((error , index) => <div key={index} className='alert alert-danger'>{error.message}</div> )}</div>}

    <form onSubmit={sendDataApi} >

  

    <label htmlFor="first_name">first_name</label>
    <input onChange={getUser} className='form-control' type="text" name='first_name' />
    <label htmlFor="last_name">last_name</label>
    <input onChange={getUser} className='form-control' type="text" name='last_name' />
    <label htmlFor="age">age</label>
    <input onChange={getUser} className='form-control' type="number" name='age' />
    <label htmlFor="email">email</label>
    <input onChange={getUser} className='form-control' type="email" name='email' />
    <label htmlFor="password">password</label>
    <input onChange={getUser}  className='form-control'type="password" name='password' />

    <button type='submit' className='btn btn-info mt-3'>Register</button>


    </form>


    </div>


    </div>
  )
}
