import React from 'react'
import { NavLink } from 'react-router-dom'
import logo from "../../imgs/logo.png"


export default function Navbar(props) {

  let y = props.user
  let x =props.logOut
  return (
    <div>
      <nav className='d-flex justify-content-between p-2 align-items-center'>
      <ul className='list-unstyled d-flex'>
      <li><NavLink className="px-2" to="/home">
        <img src={logo} alt="logo" width={70} />
        </NavLink></li>

        { y? <>
        <li><NavLink className="px-2" to="/home">Home</NavLink></li>
        <li><NavLink className="px-2" to="/movies">Movies</NavLink></li>
        <li><NavLink className="px-2" to="/tv">Tv</NavLink></li>
        <li><NavLink  className="px-2" to="/people">People</NavLink></li>
        </>:'' }
       
      </ul>
      <ul className='list-unstyled d-flex'>
          {y?<>
          <li onClick={x} className="px-2">Logout</li>
          </>:<>
          <li><NavLink  className="px-2" to="/register">Register</NavLink></li>
          <li><NavLink  className="px-2" to="/login">Login</NavLink></li>
          </>}


        
       
      </ul>
      </nav>
    </div>
  )
}
